  height: 40px;
  border-radius: 50%;
  border: none;
  background-color: #FF3E79;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-repeat: no-repeat;
  background-position: center;
  --color: white;
  transition-property: background-color, transform;
  transition-duration: 200ms;
}
.footer__btn--msg:hover, .footer__btn--profile:hover, .footer__btn--call:hover {
  transform: scale(1.1);
}
.footer__btn--profile {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}
.footer__btn--call {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.burger__wrapper {
  position: absolute;
  padding: 18px 24px 32px 24px;
  min-height: 100vh;
  min-width: 320px;
  display: flex;
  flex-flow: column;
  transform: translate(-100%, 0);
  -webkit-transform: translate(-100%, 0);
  -moz-transform: translate(-100%, 0);
  -ms-transform: translate(-100%, 0);
  -o-transform: translate(-100%, 0);
  transition: transform 300ms;
  -webkit-transition: transform 300ms;
  -moz-transition: transform 300ms;
  -ms-transition: transform 300ms;
  -o-transition: transform 300ms;
}
.burger__wrapper--open {
  display: flex;
  flex-direction: column;
  z-index: 4;
  position: fixed;
  transform: translate(0, 0);
  -webkit-transform: translate(0, 0);
  -moz-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  -o-transform: translate(0, 0);
  top: 0;
  background-color: #FFFFFF;
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
}
.burger__header {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}
.burger__logo {
  display: inline-block;
  margin-left: 17px;
  width: 97px;
  height: 55px;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_8___}) no-repeat;
}
.burger__btn {
  position: relative;
}
.burger__btn:last-child {
  margin-left: auto;
}
.burger__footer {
  margin-top: 32px;
}
.burger__contact-us {
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
}
.burger__contacts {
  clear: both;
  color: #1B1C21;
  font-weight: bold;
  margin-bottom: 40px;
}
.burger__main {
  margin-bottom: 32px;
}

.contact-us__btn:not(.contact-us__btn:first-child) {
  margin-left: 16px;
}

.aside-services {
  margin: 0;
  padding: 0;
  margin-top: 24px;
  list-style: none;
  display: flex;
  flex-flow: column;
  font-size: 16px;
  color: #1B1C21;
  row-gap: 32px;
}
.aside-services__item {
  display: block;
  position: relative;
  transition: color 400ms;
}
.aside-services__item:hover:not(.aside-services__item--active) {
  color: #FF3E79;
}
.aside-services__item:not(.aside-services__item--active):hover::before {
  content: "";
  height: 32px;
  width: 2px;
  position: absolute;
  left: -20px;
  top: -8px;
  background: #FF3E79;
  opacity: 0.2;
  transform: matrix(-1, 0, 0, 1, 0, 0);
}
.aside-services__item--active {
  color: #7E7E82;
}
.aside-services__item--active:before {
  content: "";
  height: 32px;
  width: 4px;
  position: absolute;
  left: -20px;
  top: -8px;
  background: #41F6D7;
  transform: matrix(-1, 0, 0, 1, 0, 0);
}
.aside-services__item--active:before {
  content: "";
  height: 32px;
  width: 4px;
  position: absolute;
  left: -20px;
  top: -8px;
  background: #41F6D7;
  transform: matrix(-1, 0, 0, 1, 0, 0);
}

.contacts__email {
  display: block;
  font-size: 16px;
  margin-bottom: 12px;
}
.contacts__phone {
  display: block;
  font-size: 24px;
}
.contacts__phone:hover, .contacts__email:hover {
  color: #FF3E79;
}

.language__items {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-start;
  color: #1B1C21;
  font-size: 16px;
}
.language__checkbox {
  padding: 0;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  position: absolute;
}
.language__item {
  transition: color 400ms;
  margin-right: 7px;
}
.language__checkbox:checked + .language__item {
  color: #7E7E82;
}
.language__item:hover {
  color: #FF3E79;
}

@media (min-width: 768px) {
  .burger__overflow--open {
    position: fixed;
    top: 0;
    width: 100vw;
    min-height: 100vh;
    background: #FFFFFF;
    opacity: 0.96;
    z-index: 3;
    overflow-y: auto;
    overflow-x: hidden;
  }
  .burger__wrapper--open {
    z-index: 3;
    width: 320px;
    opacity: 1;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
  }
}
@media (min-width: 1120px) {
  .burger {
    float: left;
    width: 320px;
  }
  .burger__wrapper {
    position: fixed;
    width: 320px;
    top: 0;
    transform: translate(0);
    -webkit-transform: translate(0);
    -moz-transform: translate(0);
    -ms-transform: translate(0);
    -o-transform: translate(0);
  }
  .burger__btn--close {
    display: none;
  }
  .burger__footer {
    margin-top: auto;
  }
}
.burger__btn--search {
  cursor: pointer;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background-color: #FF3E79;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
  background-repeat: no-repeat;
  background-position: center;
  --color: \$color-bacground-white;
  transition-property: background-color;
  transition-duration: 200ms;
}

.prices-table {
  display: none;
}

@media (min-width: 768px) {
  .prices-table {
    display: block;
    text-align: start;
  }
  .prices-table__title {
    padding-left: 32px;
    box-sizing: border-box;
    height: 56px;
    font-size: 16px;
    line-height: 24px;
    margin: 0;
    margin-bottom: 16px;
    color: #7E7E82;
    font-weight: 400;
    background: linear-gradient(-270deg, #F8F8F8 0%, #FFFFFF 98.61%);
    border-top: 1px solid #D9FFF5;
    padding-top: 16px;
  }
  .prices-table__table {
    width: 95%;
    margin-left: 32px;
    border-collapse: collapse;
    background: #FFFFFF;
    border: none;
    border-radius: 6px;
  }
  .prices-table__columns-title {
    background: #F8F8F8;
  }
  .prices-table__column-title {
    font-size: 14px;
    line-height: 16px;
    color: #7E7E82;
    mix-blend-mode: normal;
    opacity: 0.72;
    height: 65px;
  }
  .prices-table__column-title:nth-child(1) {
    padding-left: 24px;
    width: 40%;
  }
  .prices-table__column-title:nth-child(2) {
    width: 25%;
  }
  .prices-table__column-title:nth-child(3) {
    width: 20%;
  }
  .prices-table__column-title:nth-child(4) {
    width: 15%;
  }
  .prices-table__row {
    position: relative;
    margin: 0;
    padding: 0;
    height: 65px;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    color: #1B1C21;
  }
  .prices-table__row:not(:last-child) {
    box-shadow: 0px 33px 0 -32px #EAEAEA;
  }
  .prices-table__service {
    padding-left: 24px;
  }
  .prices-table__gloss {
    margin-left: 32px;
    margin-top: 16px;
    width: 420px;
  }
  .prices-table__text {
    color: #1B1C21;
  }
  .prices-table__proposal {
    cursor: pointer;
    margin-top: 24px;
    display: inline-block;
    position: relative;
    font-size: 16px;
    color: #1B1C21;
    font-weight: 600;
    letter-spacing: -0.2px;
  }
  .prices-table__proposal:hover {
    color: #FF3E79;
  }
  .prices-table__proposal::after {
    content: "";
    position: absolute;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_7___}) no-repeat;
    width: 12px;
    height: 12px;
    transform: translate(10px, 3px);
    -webkit-transform: translate(10px, 3px);
    -moz-transform: translate(10px, 3px);
    -ms-transform: translate(10px, 3px);
    -o-transform: translate(10px, 3px);
  }
}
.sidebar {
  position: fixed;
  width: 100vw;
  height: 100vh;
  right: -150%;
  top: 0;
  transition: all 300ms;
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  -ms-transition: all 300ms;
  -o-transition: all 300ms;
}
.sidebar__wrapper {
  position: fixed;
  height: 100vh;
  min-width: 320px;
  max-width: 440px;
  padding: 24px 16px;
  background-color: #FFFFFF;
  float: right;
  box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
  opacity: 1;
}
.sidebar__btn--close {
  margin-bottom: 40px;
}
.sidebar--open {
  right: 0;
  top: 0;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 6;
}
.sidebar__overflow {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.631372549);
  opacity: 1;
  visibility: visible;
  overflow-y: auto;
  overflow-x: hidden;
  backdrop-filter: blur(5px);
  transition: 1s all ease;
}

.form {
  align-items: center;
}
.form__title {
  position: relative;
  height: 32px;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  color: #1B1C21;
  margin-bottom: 24px;
}
.form__title::before {
  content: "";
  position: absolute;
  left: -16px;
  top: -6px;
  height: 40px;
  width: 4px;
  background: #41F6D7;
  border-radius: 2px;
  transform: matrix(-1, 0, 0, 1, 0, 0);
}
.form__input {
  margin-bottom: 16px;
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82;
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 8px;
  width: 288px;
  height: 48px;
  padding: 12px 16px;
}
.form__input:focus {
  outline: 3px solid #FF3E79;
}
.form__agreement {
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82;
}
.form__personal-data-processing, .form__privacy-policy {
  color: #FF3E79;
}
.form__btn--send {
  float: right;
  margin-top: 18px;
  text-transform: uppercase;
  letter-spacing: -0.15px;
  width: 119px;
  height: 32px;
}

@media (min-width: 768px) {
  .sidebar__wrapper {
    position: relative;
  }
  .sidebar__btn--close {
    position: absolute;
    transform: translate(-74px, -6px);
    margin: 0;
    -webkit-transform: translate(-74px, -6px);
    -moz-transform: translate(-74px, -6px);
    -ms-transform: translate(-74px, -6px);
    -o-transform: translate(-74px, -6px);
  }
  .form__input {
    width: 376px;
  }
}
.sidebar__btn--close {
  cursor: pointer;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background-color: #FF3E79;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
  background-repeat: no-repeat;
  background-position: center;
  --color: white;
  transition-property: background-color;
  transition-duration: 200ms;
}

.form__input--message {
  height: 118px;
  text-align: start;
  vertical-align: top;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "TT Lakes";
  margin: 0;
  padding: 0;
  border: none;
  min-width: 320px;
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;
  height: 100vh;
}

a {
  -webkit-text-decoration: none;
  text-decoration: none;
  color: inherit;
  font-family: inherit;
}

li {
  list-style-type: none;
}

.no-scroll {
  overflow: hidden;
