;\r\n    transition-duration: 200ms;\r\n  }\r\n  \r\n  .header__button--repair {\r\n    cursor: pointer;\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    border: none;\r\n    background-color: $color-btn;\r\n    background-image: url('/src/img/repair.svg');\r\n    background-repeat: no-repeat;\r\n    background-position: center;   \r\n    --color: $color-bacground-whitewhite;\r\n    transition-property: background-color;\r\n    transition-duration: 200ms;\r\n  }\r\n  \r\n  .header__button--profile {\r\n    cursor: pointer;\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    border: none;\r\n    background-color: $color-btn;\r\n    background-image: url('/src/img/profile.svg');\r\n    background-repeat: no-repeat;\r\n    background-position: center;   \r\n    --color: $color-bacground-white;\r\n    transition-property: background-color;\r\n    transition-duration: 200ms;\r\n  }\r\n  \r\n  .header__button--checkstatus {\r\n    cursor: pointer;\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    border: none;\r\n    background-color: $color-btn;\r\n    background-image: url('/src/img/checkstatus.svg');\r\n    background-repeat: no-repeat;\r\n    background-position: center;   \r\n    --color: $color-bacground-whitewhite;\r\n    transition-property: background-color;\r\n    transition-duration: 200ms;\r\n  }\r\n  \r\n  .services__btn-repair{\r\n    cursor: pointer;\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    border: none;\r\n    background-color: $color-btn;\r\n    background-image: url('/src/img/repair.svg');\r\n    background-repeat: no-repeat;\r\n    background-position: center;   \r\n    --color: $color-bacground-white;\r\n    transition-property: background-color;\r\n    transition-duration: 200ms;\r\n  }\r\n  .services__btn-checkstatus{\r\n    cursor: pointer;\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    border: none;\r\n    background-color: $color-btn;\r\n    background-image: url('/src/img/checkstatus.svg');\r\n    background-repeat: no-repeat;\r\n    background-position: center;   \r\n    --color: $color-bacground-whitewhite;\r\n    transition-property: background-color;\r\n    transition-duration: 200ms;\r\n  }","$color-btn: #FF3E79;\r\n$color-btn-hover:#f0c8d4;\r\n$color-bacground-grey:#F8F8F8;\r\n$color-bacground-white: #FFFFFF;\r\n$color-text: #1B1C21;\r\n$color-subtitle-text: #7E7E82;\r\n$color-card-border: #EAEAEA;\r\n$color-pgn: #DDDDDD;\r\n$color-pgn-active: #B5B6BC;\r\n$color-title-border: #D9FFF5;\r\n$color-decorate-green:#41F6D7;\r\n$color-item-active:#B8FFEC;"," @import '../fonts/fonts.css';\r\n @import '/src/scss/header.scss';\r\n @import '/src/scss/button/_button_round.scss';\r\n @import '/src/scss/button/_button_oval.scss';\r\n @import '/src/scss/services.scss';\r\n @import '/src/scss/about.scss';\r\n @import '/src/scss/repair.scss';\r\n @import '/src/scss/repair-brands.scss';\r\n @import '/src/scss/repair-types.scss';\r\n @import '/src/scss/repair-price.scss';\r\n @import '/src/scss/footer.scss';\r\n @import '/src/scss/menu.scss';\r\n @import '/src/scss/price.scss';\r\n @import '/src/scss/form.scss';\r\n @import '/src/scss/form-feedback.scss';\r\n html {\r\n     box-sizing: border-box;\r\n }\r\n \r\n *,\r\n *:before,\r\n *:after {\r\n     box-sizing: inherit;\r\n }\r\n \r\n body {\r\n     font-family: \"TT Lakes\";\r\n     margin: 0;\r\n     padding: 0;\r\n     border: none;\r\n     min-width: 320px;\r\n     max-width: 1600px;\r\n     margin-left: auto;\r\n     margin-right: auto;\r\n     height: 100vh;\r\n }\r\n \r\n a {\r\n     -webkit-text-decoration: none;\r\n     text-decoration: none;\r\n     color: inherit;\r\n     font-family: inherit;\r\n }\r\n \r\n li {\r\n     list-style-type: none;\r\n }\r\n \r\n .no-scroll {\r\n     overflow: hidden;\r\n }",".btn {\r\n    &--round {\r\n        cursor: pointer;\r\n        // padding: 0;\r\n        // margin: 0;\r\n        height: 40px;\r\n        width: 40px;\r\n        border-radius: 50%;\r\n        border: none;\r\n        background-color: #FF3E79;\r\n        --color: white;\r\n        transition-property: background-color;\r\n        transition-duration: 200ms;\r\n        &:hover path,\r\n        &:hover {\r\n            background-color: #f0c8d4;\r\n            // --color: #FF3E79;\r\n            fill: #FF3E79;\r\n        }\r\n    }\r\n}",".btn {\r\n    &--oval {\r\n        cursor: pointer;\r\n        position: relative;\r\n        display: flex;\r\n        justify-content: space-between;\r\n        text-transform: uppercase;\r\n        letter-spacing: -0.15px;\r\n        border: none;\r\n        color: #FFFFFF;\r\n        width: 119px;\r\n        height: 32px;\r\n        border-radius: 16px;\r\n        background-color: #ff3e79;\r\n        -webkit-border-radius: 16px;\r\n        -moz-border-radius: 16px;\r\n        -ms-border-radius: 16px;\r\n        -o-border-radius: 16px;\r\n        // text-align: start;\r\n        padding: 4px 12px;\r\n        align-items: center;\r\n        transition: all 300ms;\r\n        -webkit-transition: all 300ms;\r\n        -moz-transition: all 300ms;\r\n        -ms-transition: all 300ms;\r\n        -o-transition: all 300ms;\r\n        //border: 1px solid #ff3e79;\r\n        &:hover {\r\n            color: #ff3e79;\r\n            background-color: #f0c8d4;\r\n        }\r\n        &:hover path,\r\n        &:hover {\r\n            background-color: #f0c8d4;\r\n            // --color: #FF3E79;\r\n            fill: #FF3E79;\r\n        }\r\n    }\r\n}",".main {\r\n    &__services {\r\n        background-color: $color-bacground-grey;\r\n        display: flex;\r\n        flex-direction: column;\r\n        border-top: 1px $color-title-border solid;\r\n    }\r\n}\r\n\r\n.services {\r\n    margin-top: 88px;\r\n    padding-top: 24px;\r\n    &__title {\r\n        display: flex;\r\n        align-items: center;\r\n        &::before {\r\n            content: \"\";\r\n   